<template>
	<view>

		<view class="box">

			<view class="box-in">

				<view style="
								text-align: center;
								">

					<view style="height: 20rpx;"></view>

					<view>
						<text style="
									vertical-align: top;
									margin-left: 20rpx;
									font-size: 35rpx;
									">余额</text>
						<o-money size="50rpx"></o-money>
					</view>

					<view style="font-size: 60rpx;
									vertical-align: top;margin-left: 20rpx;
									color: #000;
									">{{ balanceSelfMap["oMoney"] }}</view>

				</view>

				<view v-if="false" style="background-color: #ffffff;" class="content-view">
					<u-grid :border="true" :col="3">
						<u-grid-item>
							<view class="grid-item">
								<view class="grid-item-number">2</view>
								<view class="grid-item-title">自己充值</view>
							</view>
						</u-grid-item>
						<u-grid-item>
							<view class="grid-item">
								<view style="left: -5rpx;" class="grid-item-line"></view>
								<view class="grid-item-number">3</view>
								<view class="grid-item-title">平台赠送</view>
							</view>
						</u-grid-item>
						<u-grid-item>
							<view class="grid-item">
								<view style="left: -5rpx;" class="grid-item-line"></view>
								<view class="grid-item-number">0</view>
								<view class="grid-item-title">兑换码获取</view>
							</view>
						</u-grid-item>
					</u-grid>
					<view style="height: 20rpx;"></view>
				</view>
				<view style="height: 10rpx;"></view>

				<view 
				@click="$kt.util.navTo('/pages/public/o-money-detail')"
				class="r-button">
					<text>O币明细</text>
					<image class="right-img" :src="$kt.file.byPath('image/icon/arrowRight.svg')"></image>
				</view>

				<view style="height: 10rpx;"></view>

			</view>

		</view>

		<view class="box-in">
			<view style="
					padding: 15rpx 15rpx 0 15rpx;
					box-sizing: border-box;
					">
				<u-grid :col="3">
					<u-grid-item v-for="(item, index) in oMoneyList" @click="selectOMoney(item)">
						<view
							:class="oMoneySelect.id == item.id ? 'grid-item-content grid-item-content-select' : 'grid-item-content'">
							<view class="grid-item-content-item">
								<view class="grid-item-content-item-text">
									<text style="font-size: 40rpx;font-weight: bold;vertical-align: top;">
										{{ item.num }}
									</text>
									<o-money size="50rpx"></o-money>
								</view>
								<view class="grid-item-content-item-pice">
									<text class="symbol">￥</text>
									{{ item.amount }}
								</view>
							</view>
						</view>
					</u-grid-item>
				</u-grid>
				<view style="padding: 30rpx;box-sizing: border-box;">
					<view>
						<kt-button 
						ref="ktButtonBuy"
						v-if="oMoneySelect.id != null"
						@click="buy(oMoneySelect.id)"
						>确认充值</kt-button>
						
						<kt-button 
						v-if="oMoneySelect.id == null"
						>未选择充值选项</kt-button>
						
					</view>
					<view style="height: 20rpx;"></view>
					<view style="text-align: center;color: #999;font-size: 26rpx;">充值即代表已阅读并同意<text
							@click="toWebview($kt.file.byPath('/agreement/OMoneyRechargeAgreement.html'))"
							style="color: #666;">《O币充值条款》</text></view>
				</view>
			</view>
		</view>

		<view style="padding: 0 30rpx 0;">
			<u-divider></u-divider>
		</view>

		<view style="text-align: center;">
			<!-- 使用兑换码进行兑换 -->
			<view class="re-button" style="text-align: center;">
				<view style="height: 10rpx;"></view>
				<view style="color: rgba(255,51,105,.7);" class="re-button-text">使用兑换码</view>
				<view style="height: 10rpx;"></view>
				<image style="width: 100rpx;" mode="widthFix" :src="$kt.file.byPath('image/icon/redemptionCode.svg')">
				</image>
			</view>

		</view>
		<view style="height: 30rpx;"></view>
		<kt-pay ref="ktPay"></kt-pay>
	</view>
</template>

<script>
export default {
	data() {
		return {
			ovoUserSelf: {},
			balanceSelfMap: {},
			oMoneyList: [],
			oMoneySelect: {
				id: null
			},
		};
	},
	created() {
		this.ovoUserSelf = this.$ovo.getSelf();
		this.balanceSelfMap = this.$system.getBalanceSelfMap();
		uni.$on("changeOvoSelfInfo", () => {
			this.ovoUserSelf = this.$ovo.getSelf();
		});
		uni.$on("changeBalanceSelfMap", () => {
			this.balanceSelfMap = this.$system.getBalanceSelfMap();
		});
		this.getOvoOMoneyList();
	},
	methods: {
		toWebview(src) {
			this.$kt.util.toWebview(src);
		},
		getOvoOMoneyList() {
			// 先从缓存中获取，这样可以提高用户体验
			if(uni.getStorageSync("ovoOMoneyList")){
				this.oMoneyList = uni.getStorageSync("ovoOMoneyList");
			}

			// 如果缓存时间小于5分钟，就不再请求
			if(uni.getStorageSync("ovoOMoneyListTime")){
				if(new Date().getTime() - uni.getStorageSync("ovoOMoneyListTime") < 5 * 60 * 1000){
					return null;
				}
			}

			this.$kt.request.request({
				api: this.$api.business.ovo.oMoney.getOvoOMoneyList,
				stateSuccess: (res) => {
					this.oMoneyList = res.data;
					// 将列表放在缓存
					uni.setStorageSync("ovoOMoneyList", res.data);
					// 缓存时间
					uni.setStorageSync("ovoOMoneyListTime", new Date().getTime());
				},
			});
		},
		selectOMoney(item) {
			this.oMoneySelect = item;
		},
		buy(id) {
			this.$refs.ktButtonBuy.loading();
			this.$kt.request.request({
				api: this.$api.business.ovo.oMoney.buy,
				data: {
					id
				},
				stateSuccess: (res) => {
					this.$refs.ktPay.toPay(res.data);
					this.$refs.ktButtonBuy.toNone();
				},
			});
		},
	},
}
</script>

<style lang="scss" scoped>
.box {

	.box-in {
		padding: 30rpx;
		background-color: #fff;
		border-radius: 20rpx;
		box-sizing: border-box;
	}
}

.r-button {
	position: relative;
	background-color: #f5f5f5;
	padding: 20rpx;
	color: #666;
	border-radius: 10rpx;
	font-size: 28rpx;

	.right-img {
		position: absolute;
		right: 30rpx;
		top: 50%;
		transform: translateY(-50%);
		width: 28rpx;
		height: 28rpx;
	}
}

.r-button:active {
	opacity: 0.5;
}


.grid-item-content {
	position: relative;
	display: inline-block;
	text-align: center;
	color: #000;
	border-radius: 10rpx;
	margin: 0rpx 0 40rpx 0;
	width: 180rpx;
	height: 180rpx;
	border: 3rpx solid #eee;

	.grid-item-content-item {
		position: relative;
		width: 100%;
		height: 100%;
		border-radius: 10rpx;
	}

	.grid-item-content-item-text {
		position: absolute;
		top: 40rpx;
		width: 100%;
	}

	.grid-item-content-item-pice {
		position: absolute;
		bottom: 20rpx;
		right: 10rpx;
		background-color: #f5f5f5;
		border-radius: 10rpx;
		padding: 5rpx 20rpx 5rpx 20rpx;
		border-radius: 20rpx;
		font-size: 26rpx;
		left: 50%;
		width: 50%;
		color: #666;
		font-weight: bold;
		transform: translateX(-50%);

		.symbol {
			font-size: 20rpx;
		}
	}

	.zs {
		font-size: 24rpx;
		color: #999;
		position: absolute;
		bottom: 85rpx;
		width: 100%;
		text-align: center;
	}
}

.grid-item-content-select {
	border: 3rpx solid #000;
}

.re-button {
	display: inline-block;

	// 颜色黑白滤镜
	filter: grayscale(100%);

	.re-button-text {
		font-size: 28rpx;
	}
}

.re-button:active {
	.re-button-text {
		opacity: 0.5;
	}
}

.content-view {
	padding: 10rpx;
	padding-top: 0rpx;
	border-radius: 10rpx;
	box-sizing: border-box;
	background-color: #fff;
	text-align: center;

	.grid-item-number {
		font-size: 40rpx;
		font-weight: bold;
		margin-bottom: 10rpx;
	}

	.grid-item-title {
		font-size: 28rpx;
		color: #333;
	}
}
</style>
